import React from 'react';


const JavascriptProjects = ()=> {
    return (
        <div className="projects">
            <h1>Javascript Projects</h1>
            <hr />
            <p className="leftText"><b>Click on the image or text links below to open the respective games or apps in a new window. Canvas elements were created using the p5JS library. I have also contributed to some DOM based games using standard javascript, created a variety of React projects, and have some familiarity with Express/MongoDB.</b></p>
            <br />
            
            
            <h2>React Applications</h2>
            <hr className="shortHR"/>
            <div className='projectTile'>
				<a  className="projectLink" href="https://aerinkayne.github.io/react_projects/#/" target="_blank" rel="noopener noreferrer">A variety of React projects.   
				</a><p className="leftText">This set of projects was created for <a href="https://www.freecodecamp.org/learn">FreeCodeCamp's</a> Front End Library certification.  They include a Quote Machine, a simple Music App, a Markup Editor, a Pomodoro Clock and a <b>CalCuLaToR</b>.  'They' said "never make one", but I made one :o </p>
			</div>
            <br />
            
            
            <h2>Canvas Games</h2>
            <hr className="shortHR"/>
            <div className='projectTile'>
                <a className="projectLink" href="https://aerinkayne.github.io/invaders.html" target="_blank" rel="noopener noreferrer">
                    <img src="./img/invadersLink1.png" alt="a canvas space invaders type shooter game" title="Space Invaders"/>
                </a>
            </div>
            <div className='projectTile'>
                <a className="projectLink" href="https://aerinkayne.github.io/seasons_p5.html" target="_blank" rel="noopener noreferrer">
                    <img src="./img/seasonsLink2.png" alt="a canvas platform game based on a Khan Academy project by Ryan Kee" title="Seasons (Khan Academy Spin-Off project based on 'Seasons' by Ryan Kee)"/>
                </a>
                <p className="leftText">These canvas games use the p5/processingJS library.  For better or for worse, I began learning JavaScript with the canvas/p5 combo.  This was enjoyable and engaging, but I think I did get a little too trapped in the canvas element at the start.  I enjoyed working out how to handle animations, object collisions, parallax effects and a variety of other tasks that cropped up as I continued to add functionality over time.  While I am currently focused on more practical full-stack web dev skills, I think canvas elements are underused and underestimated, and I absolutely want to create more canvas games and flesh the existing ones out some more in the future.  Sound files for these games came from <a href="https://freesound.org/">FreeSound.org</a> (as named and/or credited).  All sprites were created by me, using either GIMP or ArtSudio.</p>
            </div>
            <br />
            
            
            <h2>DOM Games</h2>
            <hr className="shortHR"/>
            <div className='projectTile'>
				<a className="projectLink" href="https://aerinkayne.github.io/freeCodeCampProjects/tetris/index.html" target="_blank" rel="noopener noreferrer">
				<img src="./img/tetrisLink1.png" alt="DOM tetris" title="Tetris" />
				</a>
            </div>
            <div className='projectTile'>
                <a className="projectLink" href="https://aerinkayne.github.io/freeCodeCampProjects/mineSweeper/index.html" target="_blank" rel="noopener noreferrer">
                    <img src="./img/minesweeperLink1.png" alt="DOM minesweeper" title="Minesweeper"/>
                </a>
                <p className="leftText">Both of these were created while following along with tutorials on <a href="https://www.youtube.com/channel/UC5DNytAJ6_FISueUfzZCVsw">Ania Kubów's</a> YouTube channel.  I was quite happy to get an approved pull request for some code additions on the on the Tetris game (see source code for the list of changes).</p>
            </div>
            <br />
            
            
            <h2>Other Projects</h2>
            <hr className="shortHR"/>
            <div className='projectTile'>
                <a className="projectLink" href="https://glacial-lake-89796.herokuapp.com/" target="_blank" rel="noopener noreferrer">Blog using Express, MongoDB, and AWS on heroku</a>
                <p className="leftText">This project was created primarily with the aid of <b>'Beginning Node.js, Express, and MongoDB Development'</b> by <i>Greg Lim</i>.  I was able to add search bar functionality on my own through some additional investigation into how to query documents with form data.  The project was a great review of and/or introduction to Node and some of its built-ins (http, fs, path, util) as well as some of the packages available through npm.  Data are stored with MongoDB on an AWS cluster.  Connection, schemas, and data queries/additions use the mongoose package.  Express is used for basic routing and middleware, to handle redirects, and to serve some static files.  Body-Parser is used to work with form data.  The project also involved implementing user sessions (with express-session), validation, and hashing user passwords (bcrypt).  Pages and layout content are served using Embedded JavaScript as the templating engine.  The original blog template was downloaded from startbootstrap.com.</p>
            </div>
            <div className='projectTile'>
                <a className="projectLink" href="https://aerinkayne.github.io/css-designs/" target="_blank" rel="noopener noreferrer">SASS animated abstract art</a>
            </div>
            <div className='projectTile'>
                <a className="projectLink" href="https://aerinkayne.github.io/freeCodeCampProjects/weatherApp/index.html" target="_blank" rel="noopener noreferrer">Weather App fetching from OpenWeather API</a>
            </div>
            <div className='projectTile'>
				<a  className="projectLink" href="https://aerinkayne.github.io/fishtank.html" target="_blank" rel="noopener noreferrer">
				<img src="./img/fishtankLink1.png" alt="a canvas fishtank app" title="Interactive Fishtank Canvas Element" />
				</a>
			</div>
            
        </div>
    );
};

export default JavascriptProjects;