import React from 'react';
import EvenThree from './mask_types/EvenThree';
import WideThree from './mask_types/WideThree';
import WiderThree from './mask_types/WiderThree';
import EvenFour from './mask_types/EvenFour';
import EvenFive from './mask_types/EvenFive';
import Eclipse from './mask_types/Eclipse';


const MaskRadio = (props)=> {

    return (
        <div>
            <h1>Image Masks</h1> 
            <div className="descriptionContainer">
                <p className="leftText">These image masks were created by experimenting with SASS mixins, functions and loops to generate class names for masking and framing images. It started, in part, simply as a way to become more familiar with functionalities available through SASS.</p>
            </div>

            <div className="maskContainerSection">
                
                <div className="maskedImageDisplay _4to3lg" style={{display: props.maskTheme==='even3' ? 'flex' : 'none'}}>
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    < EvenThree />
                </div>

                <div className="maskedImageDisplay _4to3lg"  style={{display: props.maskTheme==='even4' ? 'flex' : 'none'}}>
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    < EvenFour />
                </div>

                <div className="maskedImageDisplay _4to3lg"  style={{display: props.maskTheme==='even5' ? 'flex' : 'none'}}>
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    < EvenFive />
                </div>

                <div className="maskedImageDisplay _4to3lg"  style={{display: props.maskTheme==='wide3' ? 'flex' : 'none'}}>
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    < WideThree />
                </div>

                <div className="maskedImageDisplay _4to3lg"  style={{display: props.maskTheme==='wider3' ? 'flex' : 'none'}}>
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    < WiderThree />
                </div>

                <div className="maskedImageDisplay _4to3lg"  style={{display: props.maskTheme==='triU' ? 'flex' : 'none'}}>
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    <div className="triangle up"></div>
                </div>

                <div className="maskedImageDisplay _4to3lg"  style={{display: props.maskTheme==='triD' ? 'flex' : 'none'}}>
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    <div className="triangle down"></div>
                </div>
            </div>

            <div className="radioBtnContainer">
                <div>
                    <label>
                        <input type="radio" 
                                name="maskTheme" 
                                value="even3" 
                                checked={props.maskTheme==="even3"} 
                                onChange={props.onChange} />
                    3 panel, even</label>
                </div>
                <div>
                    <label>
                        <input type="radio" 
                                name="maskTheme" 
                                value="even4" 
                                checked={props.maskTheme==="even4"} 
                                onChange={props.onChange} />
                    4 panel, even</label>
                </div>
                <div>
                    <label>
                        <input type="radio" 
                                name="maskTheme" 
                                value="even5" 
                                checked={props.maskTheme==="even5"} 
                                onChange={props.onChange} />
                    5 panel, even</label>
                </div>
                <div>
                    <label>
                        <input type="radio" 
                                name="maskTheme" 
                                value="wide3" 
                                checked={props.maskTheme==="wide3"} 
                                onChange={props.onChange} />
                    3 panel, wide</label>
                </div>
                <div>
                    <label>
                        <input type="radio" 
                                name="maskTheme" 
                                value="wider3" 
                                checked={props.maskTheme==="wider3"} 
                                onChange={props.onChange} />
                    3 panel, wider</label>
                </div>
                <div>
                    <label>
                        <input type="radio" 
                                name="maskTheme" 
                                value="triU" 
                                checked={props.maskTheme==="triU"} 
                                onChange={props.onChange} />
                    upward triangle</label>
                </div>
                <div className="last">
                    <label>
                        <input type="radio" 
                                name="maskTheme" 
                                value="triD" 
                                checked={props.maskTheme==="triD"} 
                                onChange={props.onChange} />
                    downward triangle</label>
                </div>
            </div>


            <div className="maskContainerSection">
                <div className="maskContainer _4to3">
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    < EvenFour />
                </div>

                <div className="maskContainer _4to3">
                    <img src="./img/masks/pexels-photo-247431Pixabay1000.jpeg" alt="#" />
                    < WiderThree />
                </div>

                <div className="maskContainer _4to3">
                    <img src="./img/masks/trees-in-forest-during-sunset-247421.jpg" alt="#" />
                    < EvenThree />
                </div>
            </div>


            <div className="maskContainerSection">
                <div className="maskContainer _1to1">
                    <img src="./img/masks/nature-animal-wolf-wildernessPixabay.jpg" alt="#" /> 
                    <div className="triangle down"></div> 
                </div>

                <div className="maskContainer  _1to1lg">
                    <img src="./img/masks/pexels-photo-2131828QuangNguyenVinh650.jpeg" alt="#" /> 
                    <div className="triangle up"></div>
                </div>

                <div className="maskContainer _1to1">
                    <img src="./img/masks/silver-bordered-fritillary-butterfly-nature-orangePixabay.jpg" alt="#" />
                    <div className="triangle down"></div>
                </div>
            </div>


            <div className="maskContainerSection">
                <div className="maskContainer _1to1sm">
                    <img className="circle" src="./img/masks/earth-spaceJaymantri1000.jpg" alt="#" />
                </div>

                <div className="maskContainer _1to1lg">
                    <img className="circle" src="./img/masks/pexels-photo-2885320RobertoNickson.jpeg" alt="#" />
                    < Eclipse />
                </div>

                <div className="maskContainer _1to1sm">
                    <img className="circle" src="./img/masks/pexels-photo-55787DonaldTong.jpeg" alt="#" />
                </div>
            </div>
        </div>
    );
};

export default MaskRadio;